<template>
  <swiper
    :slidesPerView="'auto'"
    :centeredSlides="true"
    :spaceBetween="15"
    :pagination="{ clickable: true }"
    :loop="true"
    :modules="modules"
    :navigation="{
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      }"
    :simulateTouch="false"
    class="mySwiper"
    @slideChange="onSlideChange"
    ref="mySwiper"
  >
    <swiper-slide v-for="(video, index) in videos" :key="index">
      <video
        :src="video.src"
        :poster="video.poster"
        :id="'video_' + index"
        muted
        loop
        class="slide-video"
      ></video>
      <div class="slide-content">
        <h2 class="subtitle">{{ video.subtitle }}</h2>
        <h1 class="title">{{ video.title }}</h1>
        <a :href="video.link" class="more-link">了解更多</a>
      </div>
    </swiper-slide>
    <!-- 左右控制按钮 -->
    <div class="swiper-button-next"></div>
    <div class="swiper-button-prev"></div>
  </swiper>
</template>

<script>
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { Pagination, Navigation } from 'swiper/modules';
import { ref, onMounted } from 'vue';

export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  setup() {
    const videos = ref([
      { 
        src: 'https://cdn.mbby.top/video/1.mp4', 
        poster: './image/1.jpg',
        subtitle: '副标题 1',
        title: '主标题 1',
        link: 'link-to-more-1'
      },
      { 
        src: 'https://cdn.mbby.top/video/2.mp4', 
        poster: './image/2.jpg',
        subtitle: '副标题 2',
        title: '主标题 2',
        link: 'link-to-more-2'
      },
      { 
        src: 'https://cdn.mbby.top/video/3.mp4', 
        poster: './image/3.jpg',
        subtitle: '副标题 3',
        title: '主标题 3',
        link: 'link-to-more-3'
      },
      { 
        src: 'https://cdn.mbby.top/video/4.mp4', 
        poster: './image/4.jpg',
        subtitle: '副标题 4',
        title: '主标题 4',
        link: 'link-to-more-4'
      },
      // 其他视频及封面
    ]);

    const onSlideChange = (swiper) => {
      const currentIndex = swiper.realIndex;

      // 首先暂停和重置所有视频
      videos.value.forEach((_, index) => {
        const videoElement = document.getElementById('video_' + index);
        if (videoElement) {
          videoElement.pause();
          videoElement.currentTime = 0;
        }
      });

      // 然后播放当前幻灯片的视频
      const currentVideoElement = document.getElementById('video_' + currentIndex);
      if (currentVideoElement) {
        currentVideoElement.play().catch((error) => {
          // 处理播放错误，例如自动播放限制
          console.error('Error playing video:', error);
        });
      }
    };

    onMounted(() => {
      // 如果需要，此处可以进行额外的初始化操作
    });

    return {
      modules: [Pagination, Navigation],
      videos,
      onSlideChange,
    };
  },
};
</script>

<style scoped>
.mySwiper .swiper-slide {
  width: 1200px;
  height: 720px;
  position: relative;
}

.slide-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* 左右控制按钮样式 */
.swiper-button-next, .swiper-button-prev {
  background: transparent; /* 默认背景透明 */
  color: hsla(0, 0%, 100%, 0.5);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
  z-index: 10;
  transition: background 0.3s ease; /* 平滑背景过渡效果 */
}

.swiper-button-next:hover, .swiper-button-prev:hover {
  background: rgba(0, 0, 0, 0.5); /* 鼠标悬停时显示半透明背景 */
}

.swiper-button-next {
  right: 15px;
}

.swiper-button-prev {
  left: 15px;
}

.slide-content {
  position: absolute;
  text-shadow: 2px 2px 4px #000000cb;
  left: 50%;
  top: 24%;
  bottom: 20px;
  transform: translateX(-50%);
  text-align: center;
  color: white;
}

.subtitle {
  font-size: 18px;
  margin-bottom: 2px;
}

.title {
  font-size: 24px;
  margin-bottom: 15px;
}

.more-link {
  margin-bottom: 8px;
  color: white;
  text-decoration: none;
  font-size: 18px;
}

</style>
