// 引入 Vue Router 相关的函数
import { createRouter, createWebHashHistory } from 'vue-router'

// 引入 HomeView 组件，这通常是应用的首页组件
import HomeView from '../views/HomeView.vue'

// 定义路由规则数组
const routes = [
  {
    // 当 URL 的路径为 '/' 时，即网站的根路径
    path: '/',
    // 路由的名称，可以用于程序内部导航等
    name: 'home',
    // 这个路由所对应的组件
    component: HomeView
  },
  {
    // 当 URL 的路径为 '/about' 时
    path: '/about',
    // 路由的名称
    name: 'about',
    // 路由级别的代码分割
    // 这将为这个路由生成一个独立的 chunk (about.[hash].js)
    // 当访问这个路由时，该组件将被懒加载
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    // 当 URL 的路径为 '/testing' 时
    path: '/testing',
    name: 'testing',
    // 懒加载 TestingView 组件
    component: () => import('../views/TestingView.vue')
  },
  {
    // 当 URL 的路径为 '/ceshi' 时
    path: '/ceshi',
    name: 'ceshi',
    // 懒加载 CeShiView 组件
    component: () => import('../views/CeShiView.vue')
  }
]

// 创建路由实例
const router = createRouter({
  // 使用 hash 模式的路由历史
  // 这种模式下 URL 中会包含 '#'
  history: createWebHashHistory(),
  // 应用的路由规则
  routes
})

// 导出路由实例，以便在 Vue 应用中使用
export default router
