<template>
    <nav class="navbar" :class="{ scrolled: isScrolled }">
      <div class="navbar-container">
        <div class="navbar-brand">
          <img src="https://cdn.mbby.top//image/202312211610013.png" alt="Logo" class="logo" />
        </div>
        <ul class="navbar-menu">
          <li class="navbar-item"><a href="#">首页</a></li>
          <li class="navbar-item"><a href="#products">产品</a></li>
          <li class="navbar-item"><a href="#services">服务</a></li>
          <li class="navbar-item"><a href="#about">关于我们</a></li>
        </ul>
        <div class="navbar-right">
          <button class="search-button" @click="toggleSearch">搜索</button>
          <div class="search-box" v-if="showSearch">
            <input type="text" placeholder="搜索...">
          </div>
        </div>
      </div>
    </nav>
  </template>
  
  <script>
  export default {
    name: 'TopNavbar',
    data() {
      return {
        showSearch: false,
        isScrolled: false,
      };
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    unmounted() {
      window.removeEventListener('scroll', this.handleScroll);
    },
    methods: {
      toggleSearch() {
        this.showSearch = !this.showSearch;
      },
      handleScroll() {
        this.isScrolled = window.scrollY > 0;
      },
    },
  };
  </script>
  
  <style>
  .navbar {
    position: fixed;
    top: 0;
    width: 100%;
    transition: background-color 0.3s, color 0.3s;
    background-color: transparent;
    color: white;
    text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.5); /* 添加文本阴影 */
    z-index: 1000; /* 确保导航栏始终在最上方 */
  }
  
  .navbar.scrolled {
    background-color: rgba(255, 255, 255, 0.7);
    color: black;
    text-shadow: none; /* 滚动时移除阴影 */
  }
  
  .navbar-container {
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px 20px;
  }
  
  .navbar-brand .logo {
    height: 25px;
  }
  
  .navbar-menu {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
    margin-left: 20px;
  }
  
  .navbar-item {
    margin-left: 20px;
  }
  
  .navbar-item a {
    color: inherit;
    text-decoration: none;
    transition: color 0.3s, text-shadow 0.3s;
  }
  
  .navbar-right {
    position: relative;
    margin-left: auto;
  }
  
  .search-button {
    background: none;
    border: none;
    color: inherit;
    cursor: pointer;
    transition: color 0.3s;
  }
  
  .search-box {
    position: absolute;
    right: 0;
    top: 100%;
    background-color: #fff;
    padding: 10px;
    border: 1px solid #ddd;
    width: 300px;
  }
  </style>
  